import React, { Component } from 'react'
import { Header, Container } from 'semantic-ui-react'
import LoginRow from '../../components/loginrow'
import Simple from '../../components/simple'
export default class extends Component {
  constructor (props) {
    super(props)
    this.state = {
      passwords: [''],
      attempts: [''],
      states: [0],
      username: 'j.doe@yopmail.com'
    }
  }
  render () {
    const { username, passwords, states, attempts } = this.state
    return (
      <Simple
        title='How hard could it be?'
        description={
          <Container>
            <p>
              This lesson will demonstrate the difficulty of remembering multiple secure passwords.
              Enter a secure and memorable password in the signup prompt and then enter it again in the corresponding login prompt.
            </p>
            {
              passwords.length > 1
                ? <Container>
                  <p> Good job! Now, you'll have to enter <b>all</b> your passwords before unlocking the next row</p>
                  <Header size='small'>
                  So far, you've memorized {passwords.length - 1 === 1 ? '1 password' : (passwords.length - 1) + ' passwords'}
                  </Header>
                </Container>
                : ''
            }
          </Container>
        }
        to='/recommendation/start/'
        buttonText="I'm done, go to next section"
      >
        {
          passwords.map((password, i) =>
            <LoginRow
              key={i}
              step={i + 1}
              userDict={excludeOne(passwords, i)}
              username={username}
              password={passwords[i]}
              attempt={attempts[i]}
              onUsernameChange={un => this.setState({ username: un })}
              onPasswordChange={ps => this.setState({ passwords: updateOne(passwords, i, ps) })}
              onSignupSubmit={e => this.setState({ states: updateOne(states, i, 1) })}
              onLoginSubmit={e => {
                this.setState(({ attempts, states, passwords }) => {
                  if (attempts.every((item, u) => item === passwords[u])) {
                    return {
                      attempts: [...attempts.map(() => ''), ''],
                      states: [...states.map(() => 1), 0],
                      passwords: [...passwords, '']
                    }
                  } else if (passwords[i] === attempts[i]) {
                    return {
                      states: updateOne(states, i, 2)
                    }
                  } else {
                    return {
                      attempts: updateOne(attempts, i, ''),
                      states: updateOne(states, i, 0)
                    }
                  }
                })
              }}
              onAttemptChange={at => {
                console.log(at)
                this.setState({ attempts: updateOne(attempts, i, at) })
              }}
              onSignupClick={e => this.setState({ states: updateOne(states, i, 0) })}
              onLoginClick={e => this.setState({ states: updateOne(states, i, 1) })}
              state={states[i]}
              logSpeed={4}
              logThreshold={7}
            />
          )
        }
      </Simple>
    )
  }
}
const updateOne = (arr, i, val) => [...arr.slice(0, i), val, ...arr.slice(i + 1)]
const excludeOne = (arr, i) => [...arr.slice(0, i), ...arr.slice(i + 1)]



